@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css";

$bgColor: #9a4754;
$fgColor: whitesmoke;

* {
  box-sizing: border-box;
  font-family: Roboto;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 1fr auto;
  grid-template-columns: 2fr 4fr;
  grid-template-areas:
    "header main"
    "footer footer";
}

%section {
  padding: 1rem;
  min-height: 20vh;
}

%main {
  color: $fgColor;
  background-color: $bgColor;
}

header {
  @extend %section;
  @extend %main;
  grid-area: header;
  display: grid;
  place-items: center center;
  img {
    width: 200px;
    opacity: 0.9;
  }
  .title {
    display: grid;
    place-items: start center;
    margin: 0;
    font-weight: 500;
    letter-spacing: 0.3rem;
  }
}

main {
  @extend %section;
  @extend %main;
  grid-area: main;
  display: grid;
  place-items: center start;
  > div {
    max-width: 50vw;
    padding-left: 10vw;
    border-left: 2px solid hsla(0, 0, 100, 20%);
  }
}

footer {
  @extend %section;
  grid-area: footer;
  display: grid;
  grid-template-columns: 1fr 2fr;
  background-color: black;
  color: darken($color: $fgColor, $amount: 30%);
  padding: 2rem;
  
  .logo {
    img {
      width: 80px;
      opacity: 0.8;
    }
  }
  
  .info {
    max-width: 50vw;
    height: 100%;
    padding-left: 10vw;
    border-left: 2px solid hsla(0, 0, 100, 20%);
  }
}

@media screen and (max-width: 768px) {
  body {
    grid-template-rows: auto 1fr auto;
    grid-template-columns: 1fr;
    grid-template-areas:
      "header"
      "main"
      "footer";

    main > div {
      max-width: unset;
      border: none;
      padding: 0;
    }
  }
}
